<template>
  <v-col :class="imgClass" v-intersect="onIntersect">
    <v-img
      :class="`${currentClass} imgSize`"
      :src="require(`@/assets/${imgName}`)"
    ></v-img>
  </v-col>
</template>

<script>
export default {
  props: ["imgName", "imgcols"],
  data() {
    return {
      isIntersecting: false,
    };
  },
  computed: {
    currentClass() {
      return this.isIntersecting ? "square square-transition" : "square";
    },
    imgClass() {
      return this.imgcols ? this.imgcols : "col-md-12 col-lg-6";
    },
  },
  methods: {
    onIntersect(entries) {
      this.isIntersecting = entries[0].isIntersecting;
    },
  },
};
</script>

<style scoped>
.square {
  opacity: 0;
  transform: scale(0.8);
}

@media (prefers-reduced-motion: no-preference) {
  .square {
    transition: opacity 1.5s ease, transform 1.5s ease;
  }
}

.square-transition {
  opacity: 1;
  transform: none;
}

.imgSize {
  max-width: calc(min(90vw, 500px));
}
</style>
