<template>
  <v-col class="col-md-12 col-lg-6" v-intersect="onIntersect">
    <div :class="currentClass">
      {{ componentText }}
    </div>
  </v-col>
</template>

<script>
export default {
  props: {
    componentText: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      isIntersecting: false,
    };
  },
  computed: {
    currentClass() {
      return this.isIntersecting
        ? "square square-transition text-justify"
        : "square text-justify";
    },
  },
  methods: {
    onIntersect(entries) {
      this.isIntersecting = entries[0].isIntersecting;
    },
  },
};
</script>

<style scoped>
.square {
  opacity: 0;
  transform: scale(1.2);
}

@media (prefers-reduced-motion: no-preference) {
  .square {
    transition: opacity 1.5s ease, transform 1.5s ease;
  }
}

.square-transition {
  opacity: 1;
  transform: none;
}
</style>